@use "../../scss/variables" as *;
@use "../../scss/mixins" as *;

.featured-projects {
    display: flex;
    flex-direction: column;
    margin: 20vh 0 40vh;

    @include small-phone {
        margin-bottom: 25vh;
    }

    @include phone {
        margin-bottom: 25vh;
    }

    button, a {
        align-self: center;
    }
}

.featured-projects-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    gap: 5vw;
    margin: 5vh 0 15vh;

    @include small-phone {
        grid-template-columns: 1fr;
        margin-bottom: 7vh;
    }

    @include phone {
        grid-template-columns: 1fr;
        margin-bottom: 7vh;
    }
}